body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.nav-item:hover{color:#22d1ee}.navbar{margin:auto;display:flex;align-items:center;justify-content:space-between;flex-direction:row;min-width:100%;height:5vh;position:fixed;background-color:#0e153a;color:#3d5af1;z-index:900;border-bottom:1px solid #1f2a62}.nav-item{cursor:pointer;padding-right:30px;display:none}@media (min-width: 650px){.nav-item{display:inline-block}}@media (min-width: 460px){.navbar{padding-right:100px;flex-direction:row}}.right-navbar{display:flex;direction:row;font-size:18px}.left-navbar{margin-left:30px;color:#22d1ee;font-size:20px;cursor:pointer;font-weight:bold}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.projects-container{width:80%;margin:auto;display:grid;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px;grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr 1fr}@media (min-width: 1300px){.projects-container{grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr}}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.project-container{position:relative;background-color:#1f2a62;border-radius:10px;margin:20px;margin-top:25px;margin-bottom:25px;display:flex;align-items:center;justify-content:center;border:5px solid #1f2a62}@media (min-width: 550px){.project-container{border:20px solid #1f2a62}}@media (min-width: 1000px){.project-container{border:50px solid #1f2a62}}.project-img{width:80%}.hover-container{display:none}.title{color:#22d1ee;font-size:23px;font-weight:bold;background-color:#1f2a62;border-radius:5px;padding:3px}.description{color:#e2f3f5;display:none;font-size:19px;background-color:#1f2a62;border-radius:5px;padding:3px}.button-container{display:flex;flex-direction:row;width:100%;align-items:flex-end;justify-content:space-around}.live-site,.code{padding:6px;border-radius:5px;cursor:pointer;background-color:#22d1ee;color:#0e153a}.live-site:hover,.code:hover{background-color:#00e1ff}.code{background-color:#1f2a62;color:#22d1ee}.code:hover{background-color:#283da3}.button-container a{text-decoration:none;color:inherit}.project-container:hover .hover-container{display:flex;flex-direction:column;height:120%;width:110%;align-items:center;justify-content:space-between;position:absolute;z-index:900}.project-container:hover .project-img{-webkit-filter:blur(1px);filter:blur(1px);-webkit-animation:makeImageBigger 1s forwards;animation:makeImageBigger 1s forwards}@-webkit-keyframes makeImageBigger{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(1.4);transform:scale(1.4)}}@keyframes makeImageBigger{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(1.4);transform:scale(1.4)}}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.main-container{color:white;min-height:100vh;margin:auto;font-size:50px;width:100%;max-width:1600px;display:flex;align-items:center;justify-content:space-around;font-weight:lighter}.image{display:none}@media (min-width: 1100px){.image{display:block}}.typist{min-width:32%}span{color:#22d1ee}.titles-and-buttons{margin-bottom:100px}.main-title{font-size:48px;color:#e2f3f5;margin-bottom:10px;font-weight:bold}.subtitle{color:#3d5af1;font-size:30px;margin-bottom:25px;font-weight:normal}.buttons-container{display:flex;flex-direction:column;margin:20px}@media (min-width: 650px){.buttons-container{margin:0px;flex-direction:row;align-items:center;justify-content:space-between}}.see-my-work,.check-my-skills{text-align:center;border-radius:10px;cursor:pointer;font-size:20px;font-weight:bold;padding-top:20px;padding-bottom:20px;padding-right:40px;padding-left:40px;background-color:#22d1ee;color:#0e153a;margin-top:15px}@media (min-width: 650px){.see-my-work,.check-my-skills{margin-top:0px}}.see-my-work:hover,.check-my-skills:hover{background-color:#00e1ff}.check-my-skills{background-color:#1f2a62;color:#22d1ee}.check-my-skills:hover{background-color:#283da3}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.skills-container{margin-top:200px;height:100vh;display:grid;grid-template-columns:1fr;grid-auto-rows:1fr;grid-row-gap:100px;row-gap:100px}@media (min-width: 1260px){.skills-container{grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr}}.skill{display:flex;align-items:center;justify-content:space-between;margin:auto}@media (min-width: 1000px){.skill{min-width:520px}}@media (min-width: 1260px){.skill{justify-content:flex-start;margin-left:15%}}@media (min-width: 1550px){.skill{justify-content:flex-start;margin-left:30%}}.title-and-image{display:flex;flex-direction:column;align-items:center}img{width:120%}span{display:none}@media (min-width: 1000px){span{display:inline-block}}.skill-title{color:#3d5af1;font-size:30px;font-weight:bold;margin-bottom:10px}.skill-list{color:#22d1ee;font-size:19px}.tools-image-container{margin-right:30px}.tools-image{margin-bottom:50px}.frontend-image{margin-bottom:13px;width:100%}.book-image-container{margin-right:20px}ul{display:grid;grid-template-columns:1fr}@media (min-width: 1000px){ul{grid-template-columns:2fr 1fr}}ul.frontend-list{grid-template-columns:1fr}@media (min-width: 1000px){ul.frontend-list{grid-template-columns:2.5fr 1fr}}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.contact-container{margin-top:200px}.contact-main-title{font-size:100px;-webkit-transform:rotate(-55deg);transform:rotate(-55deg);max-width:30%;z-index:-20;opacity:0.1}@media (min-width: 600px){.contact-main-title{opacity:1}}h1{color:#22d1ee;font-weight:normal;font-size:22px}form{margin:auto;width:50%}textarea{width:100%;height:100px;border-radius:10px;padding-top:10px;padding-left:10px}input{border-radius:3px;border:none;margin-bottom:20px;height:20px;padding-left:10px}label{color:white;padding-right:10px;margin-bottom:20px}.btn-contact{border:none;border-radius:10px;background-color:#22d1ee;color:black;padding:10px;font-size:20px;margin-top:10px;cursor:pointer}.btn-contact:hover{background-color:#00e1ff}.btn-contact:disabled{background-color:#51afbb}

body{background-color:#0e153a;font-family:"Nunito";max-width:100vw}.dropdown{margin-right:20px;cursor:pointer}@media (min-width: 650px){.dropdown{display:none}}.dropdown-menu{position:fixed;top:0;z-index:1000;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;height:100vh;background-color:#0e153a;-webkit-transform:translateY(-100%);transform:translateY(-100%)}.dropdown-menu.open{-webkit-animation:openDropdown 0.3s forwards;animation:openDropdown 0.3s forwards}.dropdown-menu-nav-item{flex-basis:100%;margin-top:30px;color:#22d1ee;cursor:pointer}.dropdown-menu-nav-item:hover{background-color:#22d1ee;color:#1f2a62}@-webkit-keyframes openDropdown{0%{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}@keyframes openDropdown{0%{-webkit-transform:translateY(-100%);transform:translateY(-100%);opacity:0}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}

